.SliderWidget-Figcaption {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  transform: translateY(-50%); }

.SliderWidget-FigureImage {
  display: block;
  padding-bottom: 40%; }
  @media (max-width: 810px) {
    .SliderWidget-FigureImage {
      padding-bottom: 100%; } }
  .SliderWidget-FigureImage img {
    object-fit: cover; }
    @media (max-width: 810px) {
      .SliderWidget-FigureImage img {
        object-position: center; } }
