@media (max-width: 810px) {
  .MenuPage {
    background: #fff; } }

.MenuPage::before {
  z-index: -1;
  content: '';
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: var(--secondary-base-color); }
  @media (min-width: 811px) {
    .MenuPage::before {
      content: none; } }
