.CmsPage_isBreadcrumbsHidden {
  margin-top: calc(var(--header-total-height) + 1.2rem); }
  @media (max-width: 810px) {
    .CmsPage_isBreadcrumbsHidden {
      margin-top: calc(var(--header-total-height) + 1.4rem); } }

.CmsPage-Content ul,
.CmsPage-Content ol {
  margin-top: 1rem; }

.CmsPage-Wrapper {
  grid-template-columns: auto;
  max-width: var(--content-wrapper-width);
  padding-left: 1.2rem;
  padding-right: 1.2rem;
  margin-top: calc(var(--header-total-height) + 1.2rem);
  margin-bottom: var(--header-nav-height);
  margin-left: auto;
  margin-right: auto; }
  @media (max-width: 810px) {
    .CmsPage-Wrapper {
      padding-left: 1.4rem;
      padding-right: 1.4rem;
      margin-top: calc(var(--header-total-height) + 1.4rem); } }
  @media (min-width: 811px) {
    .CmsPage-Wrapper {
      margin-top: 2.4rem; } }
  .CmsPage-Wrapper_page_width_full {
    max-width: 100%;
    padding-left: 0;
    padding-right: 0; }

.CmsPage-Heading {
  margin-bottom: 1.8rem; }
  @media (max-width: 810px) {
    .CmsPage-Heading {
      margin-bottom: 2.1rem;
      display: none; } }

.CmsPage-SectionPlaceholder {
  background-image: var(--placeholder-image);
  background-size: var(--placeholder-size);
  animation: var(--placeholder-animation);
  margin: 0 0 10px;
  padding-bottom: 30%; }
  @media (max-width: 810px) {
    .CmsPage-SectionPlaceholder {
      padding-bottom: 60%; } }
  .CmsPage-SectionPlaceholder:first-child {
    padding-bottom: 40%; }
    @media (max-width: 810px) {
      .CmsPage-SectionPlaceholder:first-child {
        padding-bottom: 100%; } }

.CmsPage .TextPlaceholder {
  margin-bottom: 2.16rem;
  display: block; }
  @media (max-width: 810px) {
    .CmsPage .TextPlaceholder {
      margin-bottom: 2.52rem; } }

@media (max-width: 810px) {
  .CmsPage h2,
  .CmsPage h3 {
    font-size: 1.4rem; } }
