.ConfirmAccountPage {
  --content-wrapper-width: 600px; }
  @media (max-width: 810px) {
    .ConfirmAccountPage-Wrapper {
      padding-top: .6rem; } }
  @media (max-width: 810px) {
    .ConfirmAccountPage-Wrapper {
      padding-top: .7rem; } }
  .ConfirmAccountPage-Button {
    margin-top: 1.2rem; }
    @media (max-width: 810px) {
      .ConfirmAccountPage-Button {
        margin-top: 1.4rem;
        width: 100%; } }
    @media (min-width: 811px) {
      .ConfirmAccountPage-Button {
        margin-left: 1.2rem; } }
  .ConfirmAccountPage-EmailInput {
    display: none; }
  .ConfirmAccountPage-Heading {
    font-size: 2.4rem;
    margin-bottom: 1.2rem;
    margin-top: 2.4rem;
    text-align: center; }
    @media (max-width: 810px) {
      .ConfirmAccountPage-Heading {
        font-size: 2.8rem;
        margin-bottom: 1.4rem;
        margin-top: 2.8rem;
        display: none; } }
  @media (min-width: 811px) {
    .ConfirmAccountPage-Form {
      display: flex;
      align-items: flex-end;
      justify-content: center; } }
  @media (max-width: 810px) {
    .ConfirmAccountPage input {
      width: 100%; } }
