.InstallPromptIOS {
  background: var(--secondary-light-color);
  padding: 1.2rem;
  margin: 1.2rem; }
  @media (max-width: 810px) {
    .InstallPromptIOS {
      padding: 1.4rem;
      margin: 1.4rem; } }
  .InstallPromptIOS-Heading {
    margin: .6rem 0;
    text-align: center; }
    @media (max-width: 810px) {
      .InstallPromptIOS-Heading {
        margin: .7rem 0; } }
  .InstallPromptIOS-Content {
    align-items: center;
    display: flex;
    justify-content: center; }
  .InstallPromptIOS-Close {
    color: var(--secondary-dark-color);
    width: 100%;
    text-align: right;
    text-decoration: underline;
    margin-top: 5px; }
  .InstallPromptIOS-Plus {
    background: linear-gradient(#fff, #fff), linear-gradient(#fff, #fff), #7b7b86;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 53% 1px, 1px 50%;
    border-radius: 5px;
    height: 20px;
    margin: 0 4px;
    width: 20px;
    transform: scale(0.9);
    transform-origin: 50% 50%; }
  .InstallPromptIOS-Share {
    border: 2px solid #3b9cfe;
    bottom: -3px;
    height: 18px;
    margin: 0 4px;
    width: 18px;
    transform: scale(0.8);
    transform-origin: 50% 50%; }
    .InstallPromptIOS-Share::before {
      content: '';
      border-left: 2px solid #3b9cfe;
      border-top: 2px solid #3b9cfe;
      display: block;
      height: 6px;
      left: 3px;
      position: relative;
      top: -9px;
      transform: rotate(45deg);
      width: 6px; }
    .InstallPromptIOS-Share::after {
      border-left: 2px solid #3b9cfe;
      content: '';
      display: block;
      height: 16px;
      left: 6px;
      position: relative;
      top: -16px;
      width: 19px; }
  .InstallPromptIOS p {
    line-height: 1; }
