:root {
  --install-prompt-android-background: #eee; }

.InstallPromptAndroid {
  --button-background: var(--homepage-button-background);
  --button-color: var(--homepage-button-color);
  --button-border: transparent;
  --button-hover-background: var(--homepage-button-color);
  --button-hover-color: var(--homepage-button-background);
  --button-hover-border: transparent;
  --button-padding: 13px 40px;
  --button-border-width: 0;
  background: var(--install-prompt-android-background);
  padding: 1.2rem;
  margin: 1.2rem; }
  @media (max-width: 810px) {
    .InstallPromptAndroid {
      padding: 1.4rem;
      margin: 1.4rem; } }
  .InstallPromptAndroid-Content {
    font-size: 1.5rem;
    text-align: center;
    padding: 2.4rem 0 1.2rem; }
    @media (max-width: 810px) {
      .InstallPromptAndroid-Content {
        padding: 2.8rem 0 1.4rem; } }
  .InstallPromptAndroid-Close {
    position: absolute;
    z-index: 10;
    top: 0;
    right: 0;
    width: 3.96rem;
    height: 3.96rem; }
    .InstallPromptAndroid-Close::before, .InstallPromptAndroid-Close::after {
      content: '';
      position: absolute;
      width: 1px;
      height: 20px;
      transform-origin: 50% 50%;
      top: 7px;
      transform: rotate(-45deg);
      background-color: var(--header-color); }
    .InstallPromptAndroid-Close::after {
      transform: rotate(45deg); }
    .InstallPromptAndroid-Close::before, .InstallPromptAndroid-Close::after {
      top: 1.2rem; }
  .InstallPromptAndroid-Button {
    display: block;
    margin: auto;
    width: max-content; }
